//htmlÏóêÏÑú container ÌÉúÍ∑∏Î•º Í∞ÄÏ†∏Ïò¥
const container = document.getElementById('root');
//ÏÑúÎ≤Ñ ÌÜµÏã†ÏùÑ ÏúÑÌï¥ÏÑú XMLHttpRequest() Î≥ÄÏàò ajax Îã¥ÎäîÎã§ 
const ajax = new XMLHttpRequest();

//div ÏÉùÏÑ± ÎÇòÏ§ëÏóê ÏßÄÏö∏ ÏΩîÎìú
const div = document.createElement('div')
//URL Î≥ÄÏàòÏóê Îã¥Í∏∞
const NEW_URL = '	https://api.hnpwa.com/v0/news/1.json'; 
const NEWS_CONTENT = 'https://api.hnpwa.com/v0/item/@id.json';
ajax.open('GET' , NEW_URL , false)
ajax.send();

const newsFeed = JSON.parse(ajax.response);

//createElement ÏÉùÏÑ± 
const ul = document.createElement('ul');

//hash Ï≤¥Ïù∏ÏßÄ Ïù¥Î≤§Ìä∏Ìï®Ïàò ÎßåÎì§Í∏∞
window.addEventListener('hashchange' , function(){
    //hash ÏûêÎ¶¨Ïóê substr Î©îÏÜåÎìúÎ•º Ïù¥Ïö©Ìï¥ÏÑú ÌäπÏ†ï Î¨∏ÏûêÎ•º Ïª®Ìä∏Î°§! @idÏóêÏÑú start 1Î≤à Î¨∏Ïûê @ ÏóÜÏï†ÎäîÍ≤É
    const id = location.hash.substr(1);
    ajax.open('GET' , NEWS_CONTENT.replace('@id' , id) , false);
    ajax.send();

    const newsContent = JSON.parse(ajax.response);

    const title = document.createElement('h1');

    title.innerHTML = newsContent.title;

    div.appendChild(title)

})

for(let i = 0; i < 10; i++){
    const li = document.createElement('li');
    //a ÌÉúÍ∑∏ ÎßåÎì§Í∏∞
    const a = document.createElement('a');

    //a ÏÜçÏÑ± Í∞íÎÑ£Í≥† #Îí§Ïóê newsFeed.id Í∞íÏùÑ Ìï¥Ïâ¨ÌÉúÍ∑∏Î•º Ïù¥Ïö©Ìï¥ÏÑú ÌéòÏù¥ÏßÄ Ïù¥Îèô
    a.href = `#${newsFeed[i].id}`;
    a.innerHTML = `${newsFeed[i].title}`;
    
    //liÏóòÎ¶¨Î®ºÌä∏Ïóê aÎ•º ÏûêÏãùÏöîÏÜçÎ°ú ÎÑ£Í∏∞
    li.appendChild(a);
    ul.appendChild(li);
}


container.appendChild(ul)
container.appendChild(div)